import './Skills.css';
import Stars from '../Stars/Stars';
import html from '../../images/html.png';
import css from '../../images/css.png';
import js from '../../images/js.png';
import react from '../../images/react.png';
import git from '../../images/git.png';
import gitHub from '../../images/github.png';
import npm from '../../images/npm.png';
import bootStrap from '../../images/bootstrap.png';

const Skills = () => {
    return (
        <section className='skills' id='skills'>
            <Stars />
            <div className="skills-content">
                <div data-aos='fade-up' className='aboutme-titles'>
                    <h1 className='aboutme-title'>Skills</h1>
                    <h1 className='aboutme-title-back'>Skills</h1>
                </div>
                <div className="skills-images">
                    <div className='skills-logo-container'>
                        <img className='html-logo skills-logo' src={html} alt="html-logo" />
                        <p className='skills-logo-name'>HTML</p>
                    </div>
                    <div className='skills-logo-container'>
                        <img className='css-logo skills-logo' src={css} alt="css-logo" />
                        <p className='skills-logo-name'>CSS</p>
                    </div>
                    <div className='skills-logo-container'>
                        <img className='js-logo skills-logo' src={js} alt="js-logo" />
                        <p className='skills-logo-name'>JavaScript</p>
                    </div>
                    <div className='skills-logo-container'>
                        <img className='react-logo skills-logo' src={react} alt="react-logo" />
                        <p className='skills-logo-name'>React</p>
                    </div>
                </div>
                <div className='softskills-logos'>
                    <div className='skills-logo-container'>
                        <img className='react-logo skills-logo' src={git} alt="git-logo" />
                        <p className='skills-logo-name'>Git</p>
                    </div>
                    <div className='skills-logo-container'>
                        <img className='react-logo skills-logo' src={gitHub} alt="github-logo" />
                        <p className='skills-logo-name'>GitHub</p>
                    </div>
                    <div className='skills-logo-container'>
                        <img className='react-logo skills-logo' src={npm} alt="npm-logo" />
                        <p className='skills-logo-name'>NPM</p>
                    </div>
                    <div className='skills-logo-container'>
                        <img className='react-logo skills-logo' src={bootStrap} alt="bootstrap-logo" />
                        <p className='skills-logo-name'>Bootstrap</p>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Skills
